.down-arrw {
  position: fixed;
  top:75%;
  left: 50%;
}

.socials {
  position: fixed;
  top: 1%;
  border-radius: 8px;
  left: 30%;
  right: 30%;
  background-color: rgba(0,0,0,0.1);
  padding: 20px;
}

.socials h4 {
  display: none;
  font-weight: 300;
  opacity: 0;
}

.socials:hover h4 {
  display: inline-block;
  -webkit-animation: h4animation 0.3s ease-in;
          animation: h4animation 0.3s ease-in;
  opacity: 1;
}

@-webkit-keyframes h4animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: .8 !important;
  }
}

@keyframes h4animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: .8 !important;
  }
}

.socials i {
  margin-left: 20px;
  padding: 5px;
  border: 1px solid transparent;
  border-radius: 45%;
  background-color: rgba(255,255,255,0.3);
  width: 9% !important;
}

.socials i:hover {
  -webkit-animation: iconhover 6s ease-in;
          animation: iconhover 6s ease-in;
}

@-webkit-keyframes iconhover {
  0% {
    box-shadow: 0 0 0 0;
  }
  0% {
    box-shadow: 0 2px 20px black;
  }
}

@keyframes iconhover {
  0% {
    box-shadow: 0 0 0 0;
  }
  0% {
    box-shadow: 0 2px 20px black;
  }
}

p {
